import cambio from "../../json/monetaria/cambio/d.json";
import adr from "../../json/monetaria/adr/d.json";
import blue from "../../json/monetaria/blue/d.json";
  import trcmDates from "../../json/monetaria/cambio/dates.json";

export default {
  data() {
    return {
      megarray: [],
      chartData: {
        labels: trcmDates,
        datasets: [
 
          {
            backgroundColor: 'rgba(46,120,210,0)',
            label: "Desestacionalizado",
            data: cambio,
            borderColor: '#009966',
            pointRadius: 0,
            borderWidth: 1.5,
          },        
                    {
            backgroundColor: 'rgba(178,34,34,0.05)',
            label: "Desestacionalizado",
            data: adr,
            borderColor: '#b22222CC',
            pointRadius: 0,
            borderWidth: 1.5,
                                    fill: '-1', //fill until previous dataset

          },      
                         {
            backgroundColor: 'transparent',
            label: "Desestacionalizado",
           // data: blue,
            borderColor: 'blue',
            pointRadius: 0,
            borderWidth: 1.5,
 
          },      
        ],
      },     
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        animation: { duration: 0 },    
        layout: {
          padding: {
            left: 0,
            right: 0,
            top: 0,
            bottom: 0
          }
        },
        scales: {
          xAxes: [{
            type: 'time',
            offset: true,
            position: 'bottom',
            gridLines: {
              color: "#eee", zeroLineColor: '#eee', drawBorder: false, offsetGridLines: false, borderDash: [2, 2],
              color: "#ddd"
            },
            ticks: { fontColor: "#888", fontSize: 13, 
            
            },
            time: {
              tooltipFormat: 'DD/MM/YY',
              unit: 'year',
            }
          }],
          yAxes: [{
            ticks: { fontColor: "#888",
                 callback: function(value, index, values) {
                                return '$' +value;
                        }  },
            gridLines: { 
              color: "#eee", 
              lineWidth: 1, 
              drawBorder: false,          
            },
            scaleLabel: {
              display: false,
              labelString: 'Base 2004 = 100',
              fontColor: "#888"
            },
            position: "right",
          },
          ],
        },
        legend: {
          display: false,
        },
      },      
    };
  },
  mounted() {
     for (let i = 0; i < cambio.length; i++) {
           this.megarray.push(Math.abs((cambio[i] - adr[i])/cambio[i] * 100).toFixed(2))
     }
 
  }
};
